import { module } from './module';

class Controller {
    key: string = "devices";
    clientData: any;
    itemToolbars: Array<any> = [];
    toolbars: Array<any> = [];
    topToolbars: Array<any> = [];
    layout: string = "column";

    constructor(private $scope, private fxAction, private toolbarUtils, private materialUtils: fx.utils.materialStatic) {
        this.clientData = {
            rows: []
        };
        // this.topToolbars.push(this.toolbarUtils.noneBuilder("icon").iconBuilder(this.actionModel.icon, { fill: "black" }).toValue());
        this.topToolbars.push(this.toolbarUtils.labelBuilder(`所选项目`).attrBuilder({ flex: "" }).toValue());

        this.itemToolbars.push(
            this.toolbarUtils.btnBuilder("删除", "md-icon-button", false).iconBuilder("delete", {}).btnClick(($event, item, index) => {
                const confirm = this.$mdDialog.confirm()
                    .title(actionModel.confirm.confirmTitle)
                    .textContent(actionModel.confirm.confirmContent)
                    .ariaLabel(actionModel.confirm.confirmTitle)
                    .targetEvent($event)
                    .ok(actionModel.confirm.confirmOk || "确定")
                    .cancel(actionModel.confirm.confirmCancel || "取消");

                return this.$mdDialog.show(confirm).then(() => {
                    this.clientData.rows.splice(index, 1);
                });
            }).toValue()
        );
        this.toolbars.push(
            this.toolbarUtils.btnBuilder("改变布局方向", "md-fab md-raised md-mini", false).iconBuilder("{{qtCtl.layout=='column'?'format-vertical-align-center':'format-horizontal-align-center'}}", {}).btnClick(($event, item, index) => {
                this.layout === "column" ? this.layout = "row" : this.layout = "column";
            }).toValue()
        );
    }
}

Controller.$inject = ["$scope", "fxAction", "toolbarUtils", "materialUtils"];

function Directive(): ng.IDirective {
    return {
        restrict: 'EA',
        template: require("./tpls/query.table.jade"),
        controller: Controller,
        controllerAs: 'qtCtl',
        link: function ($scope, $element, $attrs, $ctrl) {

        }
    };
}

module.directive('fxQueryTable', Directive);


